.hero-img {
  position: absolute;
  right: 0;
  bottom: 0;
}

.button {
  border: none;
  outline: none;
}

.hero {
  background: $bg-hero-color;
  position: relative;

  .header-container {
    height: 502px;
  }
}

.hero-description,
.counter-list,
.hero-accent {
  display: none;
}

.hero-title-box {
  position: absolute;
  z-index: 5;
  top: 38%;
}

.hero-title {
  font-family: 'Titan One', sans-serif;
  font-weight: 400;
  font-size: 26px;
  line-height: 30px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $text-white-color;
  max-width: 220px;
  margin-bottom: 20px;
}

.hero-title-accent {
  color: $text-red-color;
  font-size: 48px;
  line-height: 46px;
}

.hero-list {
  display: flex;

  .hero-item {
    margin-right: 8px;

    &:not(:first-child) {
      margin-right: 0;
    }
  }
}

.hero-item {
  .button {
    display: flex;
    align-items: center;
    text-align: center;
    padding: 5px 10px;
    text-decoration: none;
    cursor: pointer;
    background: $text-red-color;
    color: $text-white-color;
    font-size: 8px;
    line-height: 1.25;
    border-radius: 20px;

    &:hover,
    &:focus {
      background: #c7133e;
    }
  }
}

.hero-item+.hero-item .button {
  background: $text-white-color;
  color: $text-red-color;

  &:hover,
  &:focus {
    background: #ecebeb;
  }
}

.hero-img {
  padding-top: 92px;
  width: 346px;
  margin-left: auto;
  display: flex;
  padding-right: 32px;

  justify-content: flex-end;
  background-image: radial-gradient(circle closest-corner at 70% 50%,
      $text-pink-color 65%,
      transparent 50%);

  .hero-main-img {
    display: inline-block;
    max-width: 177px;
    height: auto;
  }
}

@media screen and (min-width: 768px) {
  .hero {
    .hero-img {
      right: auto;
      left: 199px;
    }

    .header-container {
      position: relative;
      padding: 0 34px;
      display: flex;
      height: 399px;
    }
  }

  .hero-description,
  .counter-list,
  .hero-accent {
    display: flex;
  }

  .hero-list {
    margin-bottom: 19px;
  }

  .hero-description-img {
    max-width: 124px;
    margin-top: 19px;
  }

  .hero-title-box {
    top: 102px;
    margin-top: 0;
  }

  .hero-img {
    padding: 0;
    padding-right: 110px;
    width: 324px;
    height: 368px;
    position: absolute;
    bottom: 0px;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    background-image: radial-gradient(circle closest-corner at 64% 45%,
        #ffa5ba 68%,
        transparent 50%);

    .hero-main-img {
      margin-left: 0;
      max-width: 143px;
    }
  }

  .hero-title {
    max-width: 162px;
    font-size: 22px;
    line-height: 26px;
  }

  .hero-title-accent {
    font-size: 34px;
    line-height: 31px;
  }

  .hero-description {
    align-items: flex-end;
    display: flex;
  }

  .hero-description-section {
    display: flex;
    flex-direction: column;
    padding-bottom: 6px;
    max-width: 114px;

    .hero-description-text {
      font-weight: 400;
      font-size: 8px;
      line-height: 10px;
      color: $text-white-color;
      margin-bottom: 7px;
    }

    .button-arrow {
      padding: 0;
      width: 26px;
      height: 26px;
      border-radius: 50%;
      outline: none;
      border: none;
      cursor: pointer;
    }

    .arrow {
      width: 12px;
      height: 12px;
      fill: $text-red-color;
    }
  }

  .counter-box {
    position: absolute;
    bottom: 0;
    right: 43px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }

  .counter-list {
    display: inline-block;
    margin-left: auto;
    margin-bottom: 72px;

    .counter-item:not(:last-child) {
      margin-bottom: 13px;
    }
  }

  .counter-item {
    position: relative;
  }

  .counter-circle {
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background: $text-white-color;
    position: absolute;
    top: 0;
    right: 0;
    transform: translateX(45%) translateY(-10%);
  }

  .counter-description {
    max-width: 51px;
    font-weight: 400;
    font-size: 8px;
    line-height: 10px;
    color: $text-white-color;
    text-align: right;
    z-index: 7;
    position: relative;

    .counter-number {
      font-family: 'Titan One', sans-serif;
      font-weight: 400;
      font-size: 28px;
      line-height: 32px;
      letter-spacing: 0.04em;
      color: $text-red-color;
    }
  }

  .hero-accent-img {
    z-index: 3;
  }
}

@media screen and (min-width: 1280px) {
  .hero {
    max-width: 2000px;
    margin-left: auto;
    margin-right: auto;

    .hero-img {
      left: 286px;
    }

    .header-container {
      height: 665px;
      padding: 0 112px;
    }
  }

  .hero-list {
    margin-bottom: 56px;

    .hero-item {
      margin-right: 15px;
    }
  }

  .hero-description-img {
    max-width: 188px;
    margin-top: 56px;
  }

  .hero-description {
    .hero-item {
      margin-bottom: 45px;

      &:not(:last-child) {
        margin-right: 30px;
      }
    }
  }

  .hero-title-box {
    top: 171px;
  }

  .hero-img {
    width: 538px;
    height: 613px;
    padding-right: 183px;
    background-image: radial-gradient(circle closest-corner at 60% 43%,
        #ffa5ba 67%,
        transparent 50%);

    .hero-main-img {
      margin-left: 119px;
      max-width: 236px;
    }
  }

  .hero-title {
    max-width: 277px;
    font-size: 38px;
    line-height: 44px;
    margin-bottom: 25px;
  }

  .hero-title-accent {
    font-size: 58px;
    line-height: 52px;
  }

  .hero-item {
    .button {
      padding: 10px 20px;
      font-size: 14px;
      line-height: 1.25;
    }
  }

  .hero-description-section {
    padding-bottom: 9px;
    max-width: 200px;

    .hero-description-text {
      font-size: 14px;
      line-height: 18px;
      margin-bottom: 20px;
    }

    .button {
      padding: 0;
      width: 26px;
      height: 26px;
    }
  }

  .arrow {
    width: 10px;
    height: 10px;
  }

  .counter-box {
    right: 112px;
  }

  .counter-list {
    margin-bottom: 109px;

    .counter-item:not(:last-child) {
      margin-bottom: 30px;
    }
  }

  .counter-circle {
    width: 30px;
    height: 30px;
  }

  .counter-description {
    max-width: 92px;
    font-size: 14px;
    line-height: 18px;

    .counter-number {
      font-size: 46px;
      line-height: 53px;
    }
  }
}